.main_header_username{
  font-family: cursive;
  font-size: 24px;
  margin-left: 5px;
  text-transform: capitalize;
}
.main {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  box-shadow: -1px 14px 25px -12px rgba(0, 0, 0, 0.45);
  display: flex;
  justify-content: space-between;
  overflow: hidden;
  align-items: center;
  /* background: rgba(0, 0, 0, 0.8); */
  /* color: #ffff;
  background:#ffff; */
  max-width: 1400px;
  margin: auto;
}

  .logo {
    max-width: 100%;
    height: 60px;
    margin-left: 20px;
    padding: 7px 0;
  }
  .logout_btn{
    background: rgb(247, 189, 89);
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    color:#ffff;
    font-weight: 600;
    cursor: pointer;
    margin-right: 20px;
  }
  .logout_btn:hover{
    background: rgb(248, 56, 56); 
  }

  @media screen and (max-width: 450px) {
    .username{
      font-size: 14px;
    }
    .logo{
      height: 50px;
    }
    .logout_btn{
      padding: 8px 16px;
      font-weight: 400;
    }
  }

  @media screen and (max-width: 350px) {
    .username{
      font-size: 12px;
    }
  }
 